import React from 'react';
import { NavLink } from 'react-router-dom';

const Navbar = ()=>{
  return(
    <nav className="container-fluid navbar navbar-expand bg-light navbar-light justify-content-between py-0" >
        
        <ul className="navbar-nav">
            <li className="nav-item">
            <NavLink to='/' className="nav-link">Home</NavLink>
            </li>
        </ul>
        <ul className="navbar-nav">
            <li className="nav-item">
                <NavLink to='/dailygrand' className="nav-link">Daily Grand</NavLink>
            </li>
            <li className="nav-item">
                <NavLink to='/lotto649' className="nav-link">Lotto 649</NavLink>
            </li>
            <li className="nav-item">
                <NavLink to='/lottomax' className="nav-link">Lotto MAX</NavLink>
            </li>
        </ul>
    </nav>
  )
}

export default Navbar;

